<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full">
                        <h4 class="modal-title">{{ $t('Target') }}</h4>
                        <button type="button" class="close" @click="$emit('close')">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <Form ref="ManageRuleEngineTarget">
                            <div class="w-100 float-left" id="divTargetResultData">
                                <div class="collapse show py-3">
                                    <div class="col-sm-12 col-md-12 col-lg-8 col-xl-8 float-left pr-0">
                                        <div class="row m-0">
                                            <div class="clstargetdata_main" style="overflow:auto;">
                                                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 px-0 float-left"
                                                    style="min-height:210px; min-width:600px;" id="divTargetsdata">
                                                    <div class="clstargetdata w-100 Mainleveldiv"
                                                        v-for="(item, index) in Mainleveldiv" :key="index">
                                                        <ul class="backgroundEDEDED borderbababa col-12 coloredmandatory">
                                                            <li class="form-group align-middle px-2"><input type="text"
                                                                    :id="'OpeningBrackets_' + index" value="("
                                                                    class="form-control txtOpeningBrackets px-1"
                                                                    name="RuleTarget[0].OpeningBrackets"
                                                                    onpaste="return false;" maxlength="5"
                                                                    style="width: auto;min-width: 25px;max-width: 25px;" />
                                                            </li>
                                                            <li class="form-group align-middle px-2">
                                                                <Field :name="'RuleTarget'+index" v-slot="{errors}" :rules="{ 'required': true }" >
                                                                <select :id="'RuleTarget_FieldId' + index"
                                                                    v-model="item.RuleTarget"
                                                                    :name="'RuleTarget_FieldId' + index"
                                                                   :class="{'form-control ddlFieldId':true,'is-invalid':errors[0]}"
                                                                    style="min-width: 130px;"
                                                                    v-on:change="GetOperatorDataList($event,null,null,index)">
                                                                    <option value="">{{ $t('Select') }}</option>
                                                                    <option v-for="item in FieldsDropDownList"
                                                                        :value="item.COLUMN_ID">{{ $t(item.DISPLAY_NAME) }}
                                                                    </option>
                                                                </select>
                                                                <span v-if="errors[0]" :name="'RuleTarget'+index" class="invalid-feedback d-block">{{ errors[0] }}</span>
                                                            </Field> 
                                                            </li>
                                                            <li class="form-group align-middle px-2">
                                                                <Field :name="'OperatorId'+index" v-slot="{errors}" :rules="{ 'required': true }" >
                                                                <select :id="'OperatorId_FieldId' + index"
                                                                    v-model="item.OperatorId"
                                                                    :name="'OperatorId_FieldId' + index"
                                                                    :class="{'form-control ddlOperatorId':true,'is-invalid':errors[0]}"
                                                                    style="min-width: 130px;"
                                                                    v-on:change="SetFieldValueType($event, index)">
                                                                    <option value="">{{ $t('Select') }}</option>
                                                                    <option v-for="item in item.OperatorDataList"
                                                                        :value="item.value">
                                                                        {{ $t(item.name) }}</option>
                                                                </select>
                                                                <span v-if="errors[0]" :name="'OperatorId'+index" class="invalid-feedback d-block">{{ errors[0] }}</span>
                                                            </Field> 
                                                            </li>
                                                            <li class="form-group align-middle px-2 multiselectorsize ">
                                                                <div class="divRadio form-check form-check-inline"
                                                                    v-if="item.DataType == 'datetime' && !isShowCheckOffDays">
                                                                    <input type="radio" value="0" class="rdIsTime"
                                                                        checked="checked" name="rdIsTime" /> {{ $t('Date')
                                                                        }}
                                                                    &nbsp;
                                                                    <input type="radio" value="1" class="rdIsTime"
                                                                        name="rdIsTime" /> {{ $t('Time') }}
                                                                </div>
                                                                <div>
                                                                    <div class="input-group divfield float-left">
                                                                        <Field :name="'Condition'+index" v-slot="{errors}" :rules="{ 'required': true }" >
                                                                        <input type="text" value=""
                                                                            v-model="item.TextTypeField"
                                                                            :class="{'form-control txtFirstValue':true,'is-invalid':errors[0]}"
                                                                            :id="'ddlfirstfield_' + index"
                                                                            :name="item.TextTypeField"
                                                                            @change="AlreadyExist($event, index, item, 'txtFirstValue')"
                                                                            v-if="item.DataType == 'nvarchar' && item.selectedValue !='IsFieldService'" maxlength="10"
                                                                            style="min-width: auto;" />
                                                                        <select :id="'ddlfirstfield_' + index"
                                                                            v-model="item.selectorField"
                                                                            :name="'ddlfirstfield_' + index"
                                                                            @change="AlreadyExist($event, index, item, 'ddlfirstfield')"
                                                                            v-if="(item.DataType == 'bigint'&& item.selectType=='select') || item.selectedValue =='IsFieldService'"
                                                                            :class="{'form-control ddlfirstfield':true,'is-invalid':errors[0]}">
                                                                            <option :data-index="index" value>{{$t("Select")}}</option>
                                                                            <option v-for="item in item.DDlType"
                                                                                :value="item.value">{{ $t(item.text) }}
                                                                            </option>
                                                                        </select>
                                                                        <multiselect v-model="item.multiselector"
                                                                            v-if="item.DataType == 'bigint' && item.selectType=='multiselect'"
                                                                            :id="'ddlfirstfield_' + index" :limit="1"
                                                                            :class="{'ddlfirstfield':true,'is-invalid':errors[0]}"
                                                                            :name="'ddlfirstfield_' + index" label="text"
                                                                            track-by="value" v-bind:options="item.DDlType"
                                                                            v-bind:multiple="true" v-bind:taggable="true" v-on:input="SetCondition()" >
                                                                        </multiselect>
                                                                        <span v-if="errors[0]" :name="'Condition'+index" class="invalid-feedback d-block">{{ errors[0] }}</span>
                                                                    </Field> 
                                                                    </div>
                                                                    <div class="input-group divfield float-right">
                                                                        <input type="text" :id="'FieldSecondValue_' + index"
                                                                            value=""
                                                                            class="form-control txtSecondValue hidden"
                                                                            :name="'FieldSecondValue_' + index"
                                                                            maxlength="10" Style="min-width: auto;" />
                                                                        <a href="javascript:;" class="ancshowzone"
                                                                            style="display:none;" :title="$t('ViewMap')"><i
                                                                                class="fa fa-eye"
                                                                                style="font-size:1.5em;"></i></a>
                                                                    </div>
                                                                </div>
                                                                <div class="chkOffDays float-left"
                                                                    v-if="isShowCheckOffDays && item.DataType == 'datetime'">
                                                                    <div class="form-check form-check-inline float-left">
                                                                        <input class="custCheckBox" type="checkbox" id="1"
                                                                            value="1"
                                                                            :data-resource-id="$t('Sun')" /><label>{{
                                                                                $t('Sun') }}</label>
                                                                        <input class="custCheckBox" type="checkbox" id="2"
                                                                            value="2"
                                                                            :data-resource-id="$t('Mon')" /><label>{{
                                                                                $t('Mon') }}</label>
                                                                        <input class="custCheckBox" type="checkbox" id="3"
                                                                            value="3"
                                                                            :data-resource-id="$t('Tue')" /><label>{{
                                                                                $t('Tue') }}</label>
                                                                        <input class="custCheckBox" type="checkbox" id="4"
                                                                            value="4"
                                                                            :data-resource-id="$t('Wed')" /><label>{{
                                                                                $t('Wed') }}</label>
                                                                    </div>
                                                                    <div class="form-check form-check-inline float-left">
                                                                        <input class="custCheckBox" type="checkbox" id="5"
                                                                            value="5"
                                                                            :data-resource-id="$t('Thu')" /><label>{{
                                                                                $t('Thu') }}</label>
                                                                        <input class="custCheckBox" type="checkbox" id="6"
                                                                            value="6"
                                                                            :data-resource-id="$t('Fri')" /><label>{{
                                                                                $t('Fri') }}</label>
                                                                        <input class="custCheckBox" type="checkbox" id="7"
                                                                            value="7"
                                                                            :data-resource-id="$t('Sat')" /><label>{{
                                                                                $t('Sat') }}</label>
                                                                    </div>
                                                                </div>
                                                            </li>
                                                            <li class="form-group align-middle px-2">
                                                                <input type="text" :id="'ClosingBrackets_' + index"
                                                                    :name="'ClosingBrackets_' + index"
                                                                    class="form-control txtClosingBrackets px-1"
                                                                    onpaste="return false;" maxlength="5" value=")"
                                                                    style="width: auto;min-width: 25px;max-width: 25px;" />
                                                            </li>
                                                            <li class="form-group align-middle px-2" v-if="index != 0">
                                                                <a href="javascript:;" @click="DeleteUser(index)"
                                                                    class="recyclebin">
                                                                    <i class="fa fa-trash text-danger"></i>
                                                                </a>
                                                            </li>
                                                        </ul>
                                                        <div :class="'col-7 parentrelation float-left my-1 ' + HdnClass">
                                                            <input type="hidden" :id="'RelationWithParent_' + index"
                                                                :name="'RelationWithParent_' + index" value=""
                                                                class="hdnRelationWithParent" />
                                                            <div class="float-right">
                                                                <a style="cursor:auto;">
                                                                    <div class="blueor" v-if="item.condition">
                                                                        {{ item.condition }}</div>
                                                                </a>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div class="container-fluid float-left my-2 p-0">
                                                    <div class="col-7 text-right">
                                                        <div class="borderdddddd pull-right padding3 textaligncenter smallpops"
                                                            id="divaddmoredatabutton" v-if="!isShowAddMoreButton">
                                                            <a class="crossed" href="javascript:;"
                                                                @click="AddMoreData(true,null)" data-code="close">&#10540;</a>
                                                            <a href="javascript:;" @click="AddMoreData(null, 'or')">
                                                                <div class="colorbtn">{{ $t('OR') }}</div>
                                                            </a>
                                                            <a href="javascript:;" @click="AddMoreData(null, 'and')">
                                                                <div class="colorbtn">{{ $t('AND') }}</div>
                                                            </a>
                                                        </div>
                                                        <a href="javascript:;" id="ancAddMoreData"
                                                            @click="AddMoreData(false, null)" v-if="isShowAddMoreButton">
                                                            <i class="fa fa-plus-circle"
                                                                style="font-size: 55px; color:#7ac645;"></i>
                                                        </a>
                                                    </div>
                                                </div>
                                                <div class="search-btm-btn float-left mt-4 mb-3 border border-light">
                                                    <div class="heading text-left pl-2 float-left  py-1"
                                                        style="max-width:90px;">{{ $t('Condition') }}:</div>
                                                    <div id="divCondition"
                                                        class="boxsizing wordbreak text-left pl-2 float-left  py-1"
                                                        style="max-width:calc(100% - 100px)">{{ RuleCondition }}</div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="col-sm-12 col-md-12 col-lg-4 col-xl-4 float-left">
                                        <div class="table-responsive tableborder"
                                            style="height: 300px;max-height: 300px;min-height: 300px;">
                                            <table class="table table-bordered mb-0">
                                                <thead class="thead-bg">
                                                    <tr>
                                                        <th>{{ $t('fieldName') }}</th>
                                                        <th>{{ $t('DataType') }}</th>
                                                    </tr>
                                                </thead>
                                                <tbody id="tFieldsList">
                                                    <template v-for="fieldList in FieldsDropDownList">
                                                        <tr>
                                                            <td style="text-transform:capitalize">
                                                                {{ $t(fieldList.DISPLAY_NAME) }}</td>
                                                            <td v-if="fieldList.DATA_TYPE == 'decimal'">{{ $t('Decimal') }}
                                                            </td>
                                                            <td v-else-if="fieldList.DATA_TYPE == 'time'">{{ $t('Time') }}
                                                            </td>
                                                            <td v-else-if="fieldList.DATA_TYPE == 'tinyint'">{{
                                                                $t('Boolean') }}
                                                            </td>
                                                            <td v-else>{{ $t(fieldList.DATA_TYPE) }}</td>
                                                        </tr>
                                                    </template>
                                                </tbody>
                                            </table>
                                        </div>
                                    </div>
                                </div>
                                <div class="col-12">
                                    <div class="row flex-row-reverse">
                                        <div class="col-lg-6 text-right">
                                            <a id="btnSaveTarget" class="btn btn-success text-white" @click="btnSaveTarget"
                                                title="Save"><i class="fa fa-save pr-2"></i><span>{{ $t('Save')
                                                }}</span></a>
                                            <a id="ancTargetDialog" class="btn btn-danger text-white ml-2" title="Cancel"
                                                @click="Close"><i class="fa fa-close pr-2"></i><span>{{ $t('Cancel')
                                                }}</span></a>
                                        </div>
                                        <div class="col-lg-6 coloredmandatory_box">
                                            <div class="help-labels"><span class="text-danger"><span
                                                        class="coloredmandatory"></span>{{ $t('MandatoryTargetString')
                                                        }}</span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </Form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from "../../services/DataService";
export default {
    props: {
        acionId: {
            type: Number,
            required: true
        },
        eventIds: {
            type: String,
            required: false
        },
        moduleName: {
            type: String,
            required: true
        },
        TargetConditionData: {
            type: Array,
            required: true
        },
    },
    data() {
        return {
            isLoading: false,
            Mainleveldiv: [{ RuleTarget: '', OperatorId: '', multiselector: '', selectorField: '', TextTypeField: '', condition: '',  DataType:'nvarchar',DDlType:'',selectType:'multiselect',selectedValue:'',OperatorDataList:'' }],
            HdnClass: 'hidden',
            FieldsDropDownList: [],
            OperatorList: [],
            OperatorListData: [],
            RuleCondition: '',
            RuleDataBaseCondition:'',
            isShowCheckOffDays: false,
            isShowAddMoreButton: true,
            GetCompanyUserByCompanyId: [],
            YesorNoList: [],
            GetDDLTicketZoneByCompanyId: [],
            GetWorkTypeList: [],
            GetDDLTicketCategoryByCompanyId: [],
            GetCompanyClientByCompanyId: [],
            SlaProductList: [],
            GetDepartmentListbyCompanyForClient: []
        }
    },
    created: async function () {
        var vm = this;
        await vm.FetchData();
        await vm.GetWorkTypeListing();
        await vm.GetOperatorData(null);
        await vm.GetEditCondition();
    },
    methods: {
        FetchData: async function () {
            var vm = this;
            var url = `actionId=${vm.acionId}&eventids=${vm.eventids}&moduleName=${vm.moduleName}`;
            await DataService.AddTarget(url).then(function (response) {
                vm.FieldsDropDownList = response.data.ruleActionFieldsList;
                vm.YesorNoList = response.data.YesNoList;
                let GetCompanyUser = [];
                GetCompanyUser = response.data.companyUserByCompanyId;
                GetCompanyUser.forEach((item, index) => {
                    vm.GetCompanyUserByCompanyId.push({
                        value: item.USERID,
                        text: item.USERNAME
                    });
                });
                let GetDDLTicketCategory = [];
                GetDDLTicketCategory = response.data.GetDDLTicketCategoryByCompanyId;
                GetDDLTicketCategory.forEach((item, index) => {
                    vm.GetDDLTicketCategoryByCompanyId.push({
                        value: item.value,
                        text: item.name
                    })
                });
                let GetCompanyClient = [];
                GetCompanyClient = response.data.GetCompanyClientByCompanyId.result;
                GetCompanyClient.forEach((item, index) => {
                    vm.GetCompanyClientByCompanyId.push({
                        value: item.value,
                        text: item.name
                    })
                });
                let SlaProduct = [];
                SlaProduct = response.data.ProductsList.result;
                SlaProduct.forEach((item, index) => {
                    vm.SlaProductList.push({
                        value: item.value,
                        text: item.name
                    })
                });
                let GetDepartmentList = [];
                GetDepartmentList = response.data.GetDepartmentListbyCompanyForClient.result;
                GetDepartmentList.forEach((item, index) => {
                    vm.GetDepartmentListbyCompanyForClient.push({
                        value: item.departmenT_ID,
                        text: item.departmenT_NAME
                    })
                });
                let GetDDLTicketZone = [];
                GetDDLTicketZone = response.data.GetDDLTicketZoneByCompanyId;
                GetDDLTicketZone.forEach((item, index) => {
                    vm.GetDDLTicketZoneByCompanyId.push({
                        value: item.ZONE_ID,
                        text: item.ZONE_NAME
                    })
                });
            })
        },
        GetWorkTypeListing: async function () {
            var vm = this;
            var appendingUrl = `id=&&moduleName=${'FSM'}&type=${'FSM_WORKTYPE'}&search=&refId=`;
            await DataService.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        response.data.result.DATA.forEach((item, index) => {
                            vm.GetWorkTypeList.push({
                                value: item.value,
                                text: item.name
                            })
                        })
                    }
                }
            });
        },
        GetOperatorDataList(event,fieldName,id,index) {
            var vm = this;
            var NewOprationArray = [];
            vm.OperatorList=[];
            if(event==null){
                if (fieldName== 'category' || fieldName == 'client' || fieldName == 'Created By' || fieldName == 'product'
                || fieldName == 'department' || fieldName== 'Is Field Service' || fieldName == 'Ticket Area' || fieldName == 'TypeofWork') {
                vm.OperatorListData.forEach(item => {
                    if (item.OPERATOR_EXPRESSION == '=' && item.OPERATION_DATA_TYPE == 'bigint') {
                        NewOprationArray.push({ value: item.OPERATOR_ID, name: item.OPERATOR_DISPLAY_NAME, Expression: item.OPERATOR_EXPRESSION });
                    }
                    if ((item.OPERATOR_EXPRESSION == 'IN' || item.OPERATOR_EXPRESSION == 'NOT IN') && item.OPERATION_DATA_TYPE == 'nvarchar') {
                        NewOprationArray.push({ value: item.OPERATOR_ID, name: item.OPERATOR_DISPLAY_NAME, Expression: item.OPERATOR_EXPRESSION });
                    }

                })
                vm.OperatorList = NewOprationArray;
                vm.Mainleveldiv[index].OperatorDataList=NewOprationArray;
            }
            else {
                vm.OperatorListData.forEach(item => {
                    if (item.OPERATION_DATA_TYPE == 'nvarchar') {
                        NewOprationArray.push({ value: item.OPERATOR_ID, name: item.OPERATOR_DISPLAY_NAME, Expression: item.OPERATOR_EXPRESSION });
                    }
                })
                vm.OperatorList = NewOprationArray;
                vm.Mainleveldiv[index].OperatorDataList=NewOprationArray;
            }
            vm.Mainleveldiv[index].OperatorId='';
            vm.Mainleveldiv[index].multiselector='';
            vm.Mainleveldiv[index].TextTypeField='';
            vm.Mainleveldiv[index].selectorField='';
            vm.Mainleveldiv[index].DataType= vm.FieldsDropDownList.filter(x => x.COLUMN_ID == id)[0].DATA_TYPE;
            if (fieldName == 'category') {
                vm.Mainleveldiv[index].DDlType = vm.GetDDLTicketCategoryByCompanyId;
            } else if (fieldName == 'client') {
                vm.Mainleveldiv[index].DDlType = vm.GetCompanyClientByCompanyId;
            } else if (fieldName == 'Created By') {
                vm.Mainleveldiv[index].DDlType = vm.GetCompanyUserByCompanyId;
            } else if (fieldName == 'product') {
                vm.Mainleveldiv[index].DDlType = vm.SlaProductList;
            } else if (fieldName == 'department') {
                vm.Mainleveldiv[index].DDlType = vm.GetDepartmentListbyCompanyForClient;
            } else if (fieldName == 'Is Field Service') {
                vm.Mainleveldiv[index].DDlType = vm.YesorNoList;
                vm.Mainleveldiv[index].selectedValue = 'IsFieldService';
            } else if (fieldName == 'Ticket Area') {
                vm.Mainleveldiv[index].DDlType = vm.GetDDLTicketZoneByCompanyId;
            } else if (fieldName == 'TypeofWork') {
                vm.Mainleveldiv[index].DDlType = vm.GetWorkTypeList;
            }
            else{
                vm.DDlType='';
            }
            }else{
            if (event.target.selectedOptions[0].innerHTML == 'category ' || event.target.selectedOptions[0].innerHTML == 'client ' || event.target.selectedOptions[0].innerHTML == 'Created By ' || event.target.selectedOptions[0].innerHTML == 'product '
                || event.target.selectedOptions[0].innerHTML == 'department ' || event.target.selectedOptions[0].innerHTML == 'Is Field Service ' || event.target.selectedOptions[0].innerHTML == 'Ticket Area ' || event.target.selectedOptions[0].innerHTML == 'Type Of Work ') {
                vm.OperatorListData.forEach(item => {
                    if (item.OPERATOR_EXPRESSION == '=' && item.OPERATION_DATA_TYPE == 'bigint') {
                        NewOprationArray.push({ value: item.OPERATOR_ID, name: item.OPERATOR_DISPLAY_NAME, Expression: item.OPERATOR_EXPRESSION });
                    }
                    if ((item.OPERATOR_EXPRESSION == 'IN' || item.OPERATOR_EXPRESSION == 'NOT IN') && item.OPERATION_DATA_TYPE == 'nvarchar') {
                        NewOprationArray.push({ value: item.OPERATOR_ID, name: item.OPERATOR_DISPLAY_NAME , Expression: item.OPERATOR_EXPRESSION});
                    }

                })
                vm.OperatorList = NewOprationArray;
                vm.Mainleveldiv[index].OperatorDataList=NewOprationArray;
            }
            else {
                vm.OperatorListData.forEach(item => {
                    if (item.OPERATION_DATA_TYPE == 'nvarchar') {
                        NewOprationArray.push({ value: item.OPERATOR_ID, name: item.OPERATOR_DISPLAY_NAME, Expression: item.OPERATOR_EXPRESSION });
                    }
                })
                vm.OperatorList = NewOprationArray;
                vm.Mainleveldiv[index].OperatorDataList=NewOprationArray;
            }
            vm.Mainleveldiv[index].OperatorId='';
            vm.Mainleveldiv[index].multiselector='';
            vm.Mainleveldiv[index].TextTypeField='';
            vm.Mainleveldiv[index].selectorField='';
            vm.Mainleveldiv[index].DataType= vm.FieldsDropDownList.filter(x => x.COLUMN_ID == event.target.value)[0].DATA_TYPE;
            if (event.target.selectedOptions[0].innerHTML == 'category ') {
                vm.Mainleveldiv[index].DDlType = vm.GetDDLTicketCategoryByCompanyId;
            } else if (event.target.selectedOptions[0].innerHTML == 'client ') {
                vm.Mainleveldiv[index].DDlType = vm.GetCompanyClientByCompanyId;
            } else if (event.target.selectedOptions[0].innerHTML == 'Created By ') {
                vm.Mainleveldiv[index].DDlType = vm.GetCompanyUserByCompanyId;
            } else if (event.target.selectedOptions[0].innerHTML == 'product ') {
                vm.Mainleveldiv[index].DDlType = vm.SlaProductList;
            } else if (event.target.selectedOptions[0].innerHTML == 'department ') {
                vm.Mainleveldiv[index].DDlType = vm.GetDepartmentListbyCompanyForClient;
            } else if (event.target.selectedOptions[0].innerHTML == 'Is Field Service ') {
                vm.Mainleveldiv[index].DDlType = vm.YesorNoList;
                vm.Mainleveldiv[index].selectedValue = 'IsFieldService';
            } else if (event.target.selectedOptions[0].innerHTML == 'Ticket Area ') {
                vm.Mainleveldiv[index].DDlType = vm.GetDDLTicketZoneByCompanyId;
            } else if (event.target.selectedOptions[0].innerHTML == 'Type Of Work ') {
                vm.Mainleveldiv[index].DDlType = vm.GetWorkTypeList;
            }
            else{
                vm.DDlType='';
            }
        }
            vm.SetCondition();
        },
        GetOperatorData: async function () {
            var vm = this;
            var url = `fieldId=`;
            await DataService.GetOperators(url).then(function (response) {
                vm.OperatorListData = response.data;
            })
        },
        SetFieldValueType(event, index) {
            var vm = this;
            if(event.target.selectedOptions[0].innerHTML==' Equal To'){
                vm.Mainleveldiv[index].selectType='select';
            }
            else if(event.target.selectedOptions[0].innerHTML==' Equal to one of multiple possible values' || event.target.selectedOptions[0].innerHTML==' Not Equal to one of multiple possible values'){
                vm.Mainleveldiv[index].selectType='multiselect';
            }
            vm.SetCondition();
            vm.isShowCheckOffDays = false;
            var Obj = vm.OperatorList.filter(x => x.OPERATOR_ID == event.target.value)[0];
            if (Obj.OPERATION_DATA_TYPE == 'datetime' && (Obj.OPERATOR_DISPLAY_NAME.toLowerCase() == 'is week end' ||
                Obj.OPERATOR_DISPLAY_NAME.toLowerCase() == 'is off day' || Obj.OPERATOR_DISPLAY_NAME.toLowerCase() == 'is working day')) {
                vm.isShowCheckOffDays = true;
            }
        },
        AddMoreData(value, condition) {
            var vm = this;
            vm.Mainleveldiv[vm.Mainleveldiv.length - 1].condition = condition;
            if (condition == "or") {
                vm.Mainleveldiv.push({
                    RuleTarget: '',
                    OperatorId: '',
                    multiselector: '',
                    selectorField: '',
                    TextTypeField: '',
                    DataType:'nvarchar',
                    DDlType:'',
                    selectType:'multiselect',
                    condition: '',
                    selectedValue:'',
                    OperatorDataList:'' 
                });
                vm.HdnClass = "";
                vm.isShowAddMoreButton = true;
            } else if (condition == "and") {
                vm.Mainleveldiv.push({
                    RuleTarget: '',
                    OperatorId: '',
                    multiselector: '',
                    selectorField: '',
                    TextTypeField: '',
                    DataType:'nvarchar',
                    DDlType:'',
                    selectType:'multiselect',
                    condition: '',
                    selectedValue:'',
                    OperatorDataList:''
                });
                vm.HdnClass = "";
                vm.isShowAddMoreButton = true;
            } else {
                vm.isShowAddMoreButton = value;
            }           
        },
        SetCondition(){
            var vm =this;
            vm.RuleCondition="";
            vm.RuleDataBaseCondition="";
            vm.Mainleveldiv.forEach(item=>{
                var filterRuleName={};
                var filterOperator={};
                filterRuleName=vm.FieldsDropDownList.filter(x => x.COLUMN_ID == item.RuleTarget);
                var filterRuleNameData='';
                if(filterRuleName.length !=0 && filterRuleName.length !=''){
                    var filterRuleNameData=filterRuleName[0].DISPLAY_NAME;
                    var filterRuleColumn=filterRuleName[0].COLUMN_NAME; 
                    var filterRuleDataBind=filterRuleName[0].CONTROL_DATA_BIND_TYPE; 
                }
                filterOperator=vm.OperatorList.filter(x=> x.value==item.OperatorId);
                var filterOperatorData='';
                if(filterOperator.length !=0 && filterOperator.length !=''){
                    var filterOperatorData=filterOperator[0].name;
                    var filterOperatorExpression= filterOperator[0].Expression;
                }
                var data='';
                var dataValue='';
                if(item.multiselector !=null && item.multiselector !=''){
                    item.multiselector.forEach((itm, index)=>{
                        if(index == 0){
                         data= itm.text ;
                         dataValue=itm.value;
                        }
                        else{
                            data += ',' + itm.text;
                            dataValue += ',' + itm.value;
                        }
                    })
                    var testData = data;
                    var testDataValue=dataValue;
                    vm.RuleCondition +=`( ${filterRuleNameData} ${filterOperatorData} ${testData}) ${item.condition} `
                    vm.RuleDataBaseCondition +=`( ${filterRuleDataBind+'.'+filterRuleColumn} ${filterOperatorExpression} (${testDataValue})) ${item.condition} `
                }
                else if(item.TextTypeField !=null && item.TextTypeField !=''){
                    vm.RuleCondition +=`( ${filterRuleNameData} ${filterOperatorData} ${item.TextTypeField}) ${item.condition} `
                    vm.RuleDataBaseCondition +=`( ${filterRuleDataBind} ${filterOperatorExpression} (${"'"+item.TextTypeField+"'"})) ${item.condition} `
                }
                else if(item.selectorField !=null && item.selectorField !=''){
                var filterSelectorName={};
                filterSelectorName=item.DDlType.filter(x => x.value == item.selectorField);
                    var selectTestData = filterSelectorName[0].text;
                    var selectTestValue= filterSelectorName[0].value;
                    vm.RuleCondition +=`( ${filterRuleNameData} ${filterOperatorData} ${selectTestData}) ${item.condition} `
                    vm.RuleDataBaseCondition +=`( ${filterRuleDataBind+'.'+filterRuleColumn} ${filterOperatorExpression} (${selectTestValue})) ${item.condition} `
                }
                else{
                    vm.RuleCondition +=`( ${filterRuleNameData} ${filterOperatorData}) `
                }
           }) 
        },
        GetEditCondition(){
            var vm =this;
            var TargetConditionData=vm.TargetConditionData;
            if(TargetConditionData !=null && TargetConditionData !="" ){
                TargetConditionData.forEach((item, index)=>{
                    if(index>0){
                    vm.Mainleveldiv.push({
                    RuleTarget: '',
                    OperatorId: '',
                    multiselector: '',
                    selectorField: '',
                    TextTypeField: '',
                    DataType:'nvarchar',
                    DDlType:'',
                    selectType:'multiselect',
                    condition: '',
                    selectedValue:'',
                    OperatorDataList:''
                });
                vm.HdnClass = "";
             }
                var filterRuleName={};
                filterRuleName=vm.FieldsDropDownList.filter(x => x.COLUMN_ID == item.RuleTarget);
                var filterRuleNameData='';
                if(filterRuleName.length !=0 && filterRuleName.length !=''){
                    var filterRuleNameData=filterRuleName[0].DISPLAY_NAME;
                    vm.GetOperatorDataList(null, filterRuleNameData, item.RuleTarget,index);
                    vm.Mainleveldiv[index].RuleTarget= item.RuleTarget;
                    vm.Mainleveldiv[index].OperatorId= item.OperatorId;
                    vm.Mainleveldiv[index].multiselector= item.multiselector;
                    vm.Mainleveldiv[index].selectorField= item.selectorField;
                    vm.Mainleveldiv[index].TextTypeField= item.TextTypeField;
                    vm.Mainleveldiv[index].DataType= item.DataType;
                    vm.Mainleveldiv[index].DDlType= item.DDlType;
                    vm.Mainleveldiv[index].selectType= item.selectType;
                    vm.Mainleveldiv[index].condition= item.condition;
                    vm.Mainleveldiv[index].selectedValue= item.selectedValue;
                    vm.Mainleveldiv[index].OperatorDataList=item.OperatorDataList;
                }
                vm.SetCondition();
            });
            }
        },
        DeleteUser: function (id) {
            var vm = this;
            vm.Mainleveldiv.splice(id, 1);
            vm.Mainleveldiv[vm.Mainleveldiv.length - 1].condition = '';
            vm.SetCondition();
        },
        AlreadyExist: function (e, i, item, lbl) {
            var vm = this;
            vm.SetCondition();
        },
        Close() {
            var vm = this;
            this.$emit("close");
        },
        btnSaveTarget() {
            var vm = this;
            vm.$refs.ManageRuleEngineTarget.validate().then(result=>{
                if(result){
                var data=vm.Mainleveldiv; 
                var TargetCondition= vm.RuleCondition;
                var TargetDataBaseCondition= vm.RuleDataBaseCondition;
                vm.$emit('GetformFields', data, TargetCondition, TargetDataBaseCondition);  
                 vm.Close();
                }
            }) 
        }
    }
}
</script>